<template>
    <v-container style="position: relative; padding-top: 0px!important" fluid>
        <div v-if="isUserLoaded">
            <v-row>
                <v-btn class="top-left-fixed-button" text icon color="#FFFFFF"
                       @click="$router.go(-1)" style="z-index: 3">
                    <v-icon>mdi-arrow-left</v-icon>
                </v-btn>
                <PublicProfile
                        :profile-image="profileImg"
                        :header-image="headerImage"
                        :username="username"
                        :known-languages="knownLanguages"
                        :languages-to-learn="languagesToLearn"
                        :description="description"
                        :birth-date="birthDate"
                        :avg-rating="avgRating"
                        :num-ratings="numRatings"
                        :my-rating-is-visible="true"
                        @update-user-rating="updateUserRating"
                        :my-rating="myRating"
                        :my-u-i-d="myUID"
                        :uid="id"
                >
                </PublicProfile>
            </v-row>

            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
                    style="bottom: 76px"
            >
                {{ snackbarText }}
                <v-btn
                        color="primary"
                        text
                        @click="snackbar = false"
                >
                    {{closeText}}
                </v-btn>
            </v-snackbar>
        </div>
    </v-container>
</template>

<script>
    import PublicProfile from "../../components/PublicProfile";
    import {mapActions} from 'vuex';

    // Página de visualización de un usuario que no es el usuario autenticado
    // @group Búsqueda
    export default {
        name: "UserProfile",
        data() {
            return {
                myUID: null,
                id: null,
                profileImg: null,
                headerImage: null,
                username: null,
                knownLanguages: null,
                birthDate: null,
                languagesToLearn: null,
                description: null,
                avgRating: null,
                numRatings: null,
                myRating: null,
                snackbar: false,
                snackbarText: null,
                isUserLoaded: false,
                closeText: null,
                activeUser: null
            }
        },
        methods: {
            ...mapActions('users', ['getUserByID']),

            // @vuese
            // Actualiza la valoración del usuario autenticado al usuario representado con el valor del parámetro
            // @arg El parámetro es un Number que representa una valoración entera entre 1 y 5 o null
            updateUserRating: function (myRatingUpdated) {
                let self = this;

                self.myRating = myRatingUpdated;

                let selectedUser = this.$store.getters['users/getUserByID'](self.id);

                self.avgRating = selectedUser.avgRating;
                self.numRatings = selectedUser.numRatings;
            },

            // @vuese
            // Inicializa la información a representar del usuario
            fillProfileInfo: function () {
                let self = this;

                self.profileImg = self.activeUser.profileImg;
                self.username = self.activeUser.username;            //.setHours(0,0,0,)
                self.birthDate = this.$i18n.d(new Date(self.activeUser.birthDate.toDate()), 'short');
                self.languagesToLearn = self.activeUser.languagesToLearn;
                self.knownLanguages = self.activeUser.knownLanguages;
                self.description = self.activeUser.description;
                self.avgRating = self.activeUser.avgRating;
                self.numRatings = self.activeUser.numRatings;
                self.myRating = self.$store.getters['user/getRating'](self.id);
                self.headerImage = null;
                self.myUID = self.$store.getters["user/getID"];
            }
        },
        components: {
            PublicProfile
        },
        created() {
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');

            let self = this;
            self.id = self.$route.params.id;
            self.activeUser = this.$store.getters['users/getUserByID'](self.id);
            if (self.activeUser == undefined || self.activeUser == null) {
                self.getUserByID(self.id).then((activeUser) => {
                    self.activeUser = activeUser;
                    self.fillProfileInfo();
                    self.isUserLoaded = true;
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            } else {
                self.fillProfileInfo();
                self.isUserLoaded = true;
            }
        }
    }
</script>

<style scoped>
    .top-left-fixed-button {
        position: absolute;
        top: 0px !important;
        left: 0px !important;
        z-index: 2;
    }
</style>
